import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { IExtendedTask } from 'src/app/services';

@Component({
  selector: 'app-checklist-task-item',
  templateUrl: './checklist-task-item.component.html',
  styleUrls: ['./checklist-task-item.component.scss']
})
export class ChecklistTaskItemComponent implements OnInit {
  @Input() public taskItem: IExtendedTask;
  @Input() public firstItem: boolean;
  @Input() public isTaskEditable: boolean;
  @Output() public taskSelected = new EventEmitter();

  constructor() {}

  ngOnInit(): void {}

  public handleSelectChecklistTask(event, taskItem) {
    taskItem.isCompleted = !taskItem.isCompleted;
    event.currentTarget.checked = taskItem.isCompleted;
    this.taskSelected.emit();
  }
}
