import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import {
  standardFormattedDate,
  standardFormattedDateNoYear
} from 'src/utils/dateUtils';
import { WorkordersService } from '../services/workorder/workorder.service';
import {
  Workorder,
  WorkorderStatus
} from '../services/workorder/workorder.types';
import { Router } from '@angular/router';
import {
  AuthenticationService,
  PermissionGroup,
  ToastService,
  WorkOrdersPermissions
} from '../services';
import { debounceTime, filter } from 'rxjs/operators';
import { SearchFilterService } from '../services/search-filter/search-filter.service';
import { WorkOrderWizard } from './work-order.wizard';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-work-orders',
  templateUrl: './work-orders.component.html',
  styleUrls: ['./work-orders.component.scss']
})
export class WorkOrdersComponent implements OnInit, OnDestroy {
  public searchForm: FormGroup;
  public isLoadingWorkorders: boolean;
  public offset = 0;
  public take = 10;
  public shouldGetNextWorkorders = true;
  public workorders: Workorder[] = [];
  public standardFormattedDate = standardFormattedDate;
  public standardFormattedDateNoYear = standardFormattedDateNoYear;
  public WorkorderStatus = WorkorderStatus;
  public canCreateWorkOrder: boolean;

  constructor(
    private formBuilder: FormBuilder,
    private workorderService: WorkordersService,
    private router: Router,
    private authenticationService: AuthenticationService,
    private searchFilterService: SearchFilterService,
    private workOrderWizard: WorkOrderWizard,
    private toastService: ToastService
  ) {}

  public ngOnInit(): void {
    this.setPermissionBooleans();
    this.searchForm = this.formBuilder.group({
      text: this.searchFilterService.searchWOTextValue
    });
    this.initiateFreshSearchSettings();
    this.formChangeEvent();
    this.loadNextWorkorders();
  }

  public formChangeEvent() {
    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3 || textValue.length === 0)
      )
      .subscribe(() => {
        this.submitSearch();
      });
  }

  public initiateFreshSearchSettings(): void {
    this.workorders = [];
    this.workOrderWizard.reset();
    this.offset = 0;
    this.shouldGetNextWorkorders = true;
  }

  public loadNextWorkorders(): void {
    this.isLoadingWorkorders = true;
    this.workorderService
      .listWorkorders({
        offset: this.offset,
        take: this.take,
        text: this.searchForm.value.text
      })
      .subscribe((workorderList: Workorder[]) => {
        this.searchFilterService.searchWOTextValueCache = this.searchForm.value.text;
        this.workorders.push(...workorderList);
        this.offset += this.take;
        if (workorderList.length < this.take) {
          this.shouldGetNextWorkorders = false;
        }
        this.isLoadingWorkorders = false;
      });
  }

  public submitSearch() {
    this.initiateFreshSearchSettings();
    this.loadNextWorkorders();
  }

  public handleSelectedWorkorderClick(
    workOrderNumber: string,
    workOrder: Workorder
  ) {
    this.workOrderWizard.workOrder = workOrder;
    if (this.workOrderWizard.workOrder) {
      this.router.navigate(['work-orders', 'WO' + workOrderNumber]);
    }
  }

  public createWorkOrder() {
    this.router.navigate(['work-orders', 'create-work-order']);
  }

  private setPermissionBooleans() {
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.WorkOrders]: [WorkOrdersPermissions.CanCreateWorkOrder]
      })
      .subscribe(hasPermission => (this.canCreateWorkOrder = hasPermission));
  }

  public ngOnDestroy() {
    this.searchFilterService.initiateWOSearchFilter();
  }

  public getUserName(userDetails: string) {
    if (userDetails && userDetails.includes('-')) {
      return userDetails.split('-')[1];
    } else {
      return userDetails;
    }
  }

  public getWorkOrderCSV() {
    this.workorderService.getWorkorderCSV().subscribe(
      workOrder => {
        const MIME_TYPE = 'text/csv';
        const blob = new Blob([workOrder], { type: MIME_TYPE });
        const date = new Date(Date.now());
        const curDate = date.toLocaleString('en-IN', {
          year: 'numeric',
          month: 'numeric',
          day: 'numeric'
        });
        const a = window.document.createElement('a');
        const fileName = 'workorder';
        a.href = window.URL.createObjectURL(blob);
        a.download = `${fileName}-${curDate}.csv`;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      },
      (err: HttpErrorResponse) => {
        this.toastService.showErrorToast(
          'Unable to download the work orders CSV file.'
        );
        return;
      }
    );
  }
}
