import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { debounceTime, filter } from 'rxjs/operators';
import { ISpares } from 'src/app/services';

@Component({
  selector: 'app-spares-skus',
  templateUrl: './spares-skus.component.html',
  styleUrls: ['./spares-skus.component.scss']
})
export class SparesSkusComponent implements OnInit {
  @Input() public sparesSku: ISpares;
  @Input() public isEditable: boolean;
  public qtyForm: FormGroup;
  @Output() public isSkuDeleted = new EventEmitter<boolean>();
  @Output() public handlesQtyChange = new EventEmitter<number>();
  @Output() public isQtyInValid = new EventEmitter<boolean>();

  constructor(private formBuilder: FormBuilder) {}

  public ngOnInit(): void {
    this.qtyForm = this.formBuilder.group({
      qty: [
        this.sparesSku.quantity,
        [
          Validators.min(1),
          Validators.max(this.sparesSku.sku.quantities.inStock),
          Validators.required
        ]
      ]
    });

    this.qtyForm
      .get('qty')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue <= this.sparesSku.sku.quantities.inStock)
      )
      .subscribe((skuQty: number) => {
        this.handlesQtyChange.emit(skuQty);
        this.isQtyInValid.emit(
          this.qtyForm.value.qty > this.sparesSku.sku.quantities.inStock
        );
      });
  }

  public handleQtyClipboard(event: ClipboardEvent) {
    event.preventDefault();
    return;
  }

  public disableRightClick(e) {
    e.preventDefault();
  }

  public handleQtyInputKeyUp(event: KeyboardEvent) {
    this.isQtyInValid.emit(
      this.qtyForm.get('qty').value > this.sparesSku.sku.quantities.inStock
    );
    if (
      event.key === 'Enter' &&
      this.qtyForm.value.qty > 0 &&
      this.qtyForm.value.qty.toString().length > 0 &&
      this.qtyForm.get('qty').value > this.sparesSku.sku.quantities.inStock
    ) {
      event.preventDefault();
    }
  }

  public handleQtyInputKeyDown(event) {
    const qtyStringExceedsMaxLength =
      this.qtyForm.value.qty.toString().length >= 4;
    if (
      this.qtyForm.valid &&
      this.qtyForm.get('qty').value > 0 &&
      this.qtyForm.get('qty').value > this.sparesSku.sku.quantities.inStock &&
      event.key === 'Enter'
    ) {
      return;
    }
    if (event.key === 'Backspace' || event.key === 'Delete') {
      return;
    }
    if (qtyStringExceedsMaxLength || isNaN(event.key) || event.keyCode === 32) {
      event.preventDefault();
    }

    const currentQty: number = this.qtyForm.get('qty').value;
    this.handlesQtyChange.emit(currentQty);

    this.isQtyInValid.emit(
      this.qtyForm.get('qty').value > this.sparesSku.sku.quantities.inStock
    );
  }

  public handleDeleteItem() {
    this.isSkuDeleted.emit(true);
  }

  public handlesDecreaseQty() {
    let currentQty: number = this.qtyForm.get('qty').value;
    currentQty -= 1;
    if (currentQty <= 0) {
      return;
    }
    this.qtyForm.setValue({ qty: currentQty });

    this.handlesQtyChange.emit(currentQty);

    this.isQtyInValid.emit(
      this.qtyForm.get('qty').value > this.sparesSku.sku.quantities.inStock
    );
  }

  public handlesIncreaseQty() {
    let currentQty: number = Number(this.qtyForm.get('qty').value);
    currentQty += 1;
    if (currentQty > this.sparesSku.sku.quantities.inStock) {
      return;
    }
    this.qtyForm.setValue({ qty: currentQty });

    this.handlesQtyChange.emit(currentQty);

    this.isQtyInValid.emit(
      this.qtyForm.get('qty').value > this.sparesSku.sku.quantities.inStock
    );
  }
}
