import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators
} from '@angular/forms';
import { Router } from '@angular/router';
import {
  IExtendedWorkOrder,
  WorkOrderPriority,
  WorkorderStatus,
  WorkordersService,
  IWorkOrderServiceDetail,
  IWorkOrderCategory,
  FilehandlerService,
  IFileCollection,
  AuthenticationService,
  PermissionGroup,
  WorkOrdersPermissions,
  ToastService,
  Toast,
  Modules,
  Role,
  UserProfile,
  IAssets
} from '../../services';
import {
  standardFormattedDate,
  standardFormattedDateTime
} from 'src/utils/dateUtils';
import { enumString, enumValues } from 'src/utils/enumUtils';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import {
  fileListFileSizesValidator,
  fileListFileTypeValidator,
  fileListNumberOfFilesValidator
} from 'src/utils/formValidators';
import { WorkOrderWizard } from '../work-order.wizard';

@Component({
  selector: 'app-view-workorder',
  templateUrl: './view-workorder.component.html',
  styleUrls: ['./view-workorder.component.scss']
})
export class ViewWorkorderComponent implements OnInit {
  public isLoading = true;
  public attachedFileCollection = new BehaviorSubject<IFileCollection[]>([]);
  public extendedWorkOrder: IExtendedWorkOrder;
  public form: FormGroup;
  public standardFormattedDate = standardFormattedDate;
  public workOrderPriorities = enumValues(WorkOrderPriority);
  public WorkOrderStatus = WorkorderStatus;
  public workOrderStatusValues: number[] = enumValues(WorkorderStatus);
  public standardFormattedDateTime = standardFormattedDateTime;
  public workOrderServiceDetails: IWorkOrderServiceDetail[];
  public workOrderNumber: string;
  public detailsModalVisible = false;
  public selectedServiceUserId: string;
  public selectedServiceDateTime: string;
  public selectedServiceDetails: string;
  public textMaxLength = 90;
  public fileUploadForm: FormGroup;
  public workOrderCategories: IWorkOrderCategory[];
  public fileCount: number = 0;
  public fileSrcCollection: IFileCollection[] = [];
  public isEditable = false;
  public isUpdatingWorkOrder = false;
  public errorMessage: string;
  public isNewFilesAdded: boolean = false;
  public showSaveButton = false;
  public filesLimit = 3;
  @ViewChild('fileUploadInput')
  public fileUploadInput: ElementRef;
  public filePathCollection: string[] = [];
  public savedFileSrcCollection: any;
  public isGettingUserRights: boolean;
  public fileTypes = [
    'image/jpeg',
    'image/png',
    'application/msword',
    'jpeg',
    'jpg',
    'png',
    'doc',
    'docx',
    'xls',
    'csv',
    'application/ms-excel',
    'xlsx',
    'pdf',
    'bmp'
  ];
  public workOrderStatus: string;
  public addAssetsModalVisible = false;
  public isAssetAvailable = true;
  public enableServiceTextarea = false;
  public isShowEditIcon = true;
  public selectedWorkOrderServiceDetails: IWorkOrderServiceDetail;
  public isSubmitDisabled: boolean;
  public submitAttempted = false;
  public expanded: boolean = false;

  constructor(
    private workOrdersService: WorkordersService,
    private formBuilder: FormBuilder,
    private router: Router,
    private fileUploadService: FilehandlerService,
    private authenticationService: AuthenticationService,
    private toastService: ToastService,
    private workOrderWizard: WorkOrderWizard
  ) {
    if (this.workOrderWizard.workOrder) {
      this.workOrderStatus = this.workOrderWizard.workOrder.status.toString();
      this.workOrderNumber = this.workOrderWizard.workOrder.number.toString();
      this.checkUserEditRights();
    } else {
      this.router.navigate(['work-orders']);
    }
  }

  ngOnInit(): void {
    this.viewWorkOrderForId();
    this.viewWorkOrderServiceDetailForId();
    this.listCategory();
  }

  public createForm() {
    const validPattern = '^[a-zA-Z0-9 ]*';
    const bulidWorkOrderDetailForm = (
      extendedWorkOrder: IExtendedWorkOrder
    ) => {
      this.form = this.formBuilder.group({
        title: new FormControl(
          {
            value: extendedWorkOrder.title,
            disabled: !this.isEditable
          },
          Validators.compose([
            Validators.maxLength(50),
            Validators.required,
            Validators.pattern(validPattern)
          ])
        ),
        description: new FormControl(
          {
            value: extendedWorkOrder.description,
            disabled: !this.isEditable
          },
          Validators.compose([Validators.maxLength(4000), Validators.required])
        ),
        category: new FormControl({
          value: extendedWorkOrder.category.id,
          disabled: !this.isEditable
        }),
        reportedOn: new FormControl(
          {
            value: extendedWorkOrder.reportedOn,
            disabled: true
          },
          Validators.compose([Validators.required])
        ),
        dueDate: new FormControl(
          {
            value: extendedWorkOrder.dueDate,
            disabled: true
          },
          Validators.compose([Validators.required])
        ),
        priority: new FormControl({
          value: extendedWorkOrder.priority,
          disabled: !this.isEditable
        }),
        assignedToUser: new FormControl(
          {
            value: extendedWorkOrder.assignedToUser,
            disabled: !this.isEditable
          },
          Validators.compose([Validators.maxLength(50)])
        ),
        status: new FormControl(
          {
            value: extendedWorkOrder.status,
            disabled: !this.isEditable
          },
          Validators.compose([Validators.required])
        ),
        serviceDetails: new FormControl(
          {
            value: '',
            disabled: !this.isEditable
          },
          Validators.compose([Validators.maxLength(4000)])
        )
      });
    };
    bulidWorkOrderDetailForm(this.extendedWorkOrder);

    this.form.valueChanges.subscribe(() => {
      setTimeout(() => {
        this.updateFormChanges();
      }, 100);
    });

    this.fileUploadForm = this.formBuilder.group({
      files: [
        null,
        [
          Validators.required,
          fileListFileTypeValidator(this.fileTypes),
          fileListFileSizesValidator(2000000)
        ],
        fileListNumberOfFilesValidator(this.filesLimit)
      ]
    });
  }

  public viewWorkOrderForId(isOnInit: boolean = true) {
    this.isLoading = true;
    const workOrderAttachements$: Observable<string[]> = this.workOrdersService
      .viewWorkOrderForId(this.workOrderNumber.toString())
      .pipe(
        map(extendedWorkOrder => {
          this.extendedWorkOrder = extendedWorkOrder;
          if (isOnInit) {
            this.createForm();
          }
          return extendedWorkOrder.attachedFiles;
        })
      );
    workOrderAttachements$.subscribe(assetAttachments => {
      this.fileCount = assetAttachments ? assetAttachments.length : 0;
      let fileList: any[] = [];
      this.fileSrcCollection = [];
      this.filePathCollection = [];
      if (assetAttachments) {
        assetAttachments.forEach(filePath => {
          const fileNameWithExt = filePath.replace(/^.*[\\\/]/, '');
          const fileName = fileNameWithExt.substring(0, fileNameWithExt.length);
          if (fileName === 'blob') {
            return;
          }
          this.filePathCollection.push(filePath);
          this.fileUploadService.getFile(filePath).subscribe(fileData => {
            fileData.name = fileName;
            this.fileSrcCollection.push(fileData);
            this.fileCount = this.fileSrcCollection
              ? this.fileSrcCollection.length
              : 0;
            fileList.push(fileData);
          });
        });
        this.fileUploadForm.setValue({
          files: this.fileSrcCollection
        });
        this.attachedFileCollection.next(this.fileSrcCollection);
      }
      this.isLoading = false;
    });
  }

  public viewWorkOrderServiceDetailForId(): void {
    this.workOrdersService
      .viewWorkOrderServiceDetailForId(
        this.workOrderWizard.workOrder.number.toString()
      )
      .subscribe(workOrderServiceDetails => {
        this.workOrderServiceDetails = workOrderServiceDetails;
      });
  }

  public listCategory() {
    this.workOrdersService
      .listWorkOrderCategory()
      .subscribe((data: IWorkOrderCategory[]) => {
        this.workOrderCategories = data;
      });
  }

  public handleViewMoreClick(workOrderServiceDetail: IWorkOrderServiceDetail) {
    this.detailsModalVisible = true;
    this.isShowEditIcon = this.isEditable;
    this.selectedWorkOrderServiceDetails = workOrderServiceDetail;
  }

  public handleDetailsModalClose() {
    this.detailsModalVisible = false;
    this.enableServiceTextarea = false;
    this.addAssetsModalVisible = false;
  }

  public disableRightClick(e) {
    e.preventDefault();
  }

  public handlesPickSpares(workOrderNumber: string) {
    this.router.navigate([
      'work-orders',
      '#WO' + workOrderNumber,
      'PickSpares'
    ]);
  }

  private checkUserEditRights() {
    this.isGettingUserRights = true;
    const userRole$: Observable<UserProfile> = this.authenticationService.currentUserProfile$.pipe(
      map(userProfile => userProfile)
    );

    userRole$.subscribe(userProfile => {
      this.authenticationService
        .currentUserHasPermission({
          [PermissionGroup.WorkOrders]: [WorkOrdersPermissions.CanEditWorkOrder]
        })
        .subscribe(hasPermission => {
          if (userProfile.userRole.groupName !== Role.Technician) {
            this.isEditable =
              hasPermission &&
              Number(this.workOrderStatus) !== WorkorderStatus.Closed;
          } else {
            this.isEditable =
              hasPermission &&
              Number(this.workOrderStatus) !== WorkorderStatus.Closed &&
              Number(this.workOrderStatus) !== WorkorderStatus.Complete;
          }
          this.isGettingUserRights = false;
        });
    });
  }

  public launchFileInput(): void {
    this.fileUploadForm.reset({});
    this.fileUploadInput.nativeElement.value = '';
    this.fileUploadInput.nativeElement.click();
  }

  public updateFormChanges() {
    debugger
    this.extendedWorkOrder.status = this.form.get('status').value;
    this.extendedWorkOrder.assignedToUser = this.form.get('assignedToUser')
      .value
      ? this.form.get('assignedToUser').value.toString()
      : '';
    this.extendedWorkOrder.title = this.form.get('title').value.toString();
    this.extendedWorkOrder.description = this.form
      .get('description')
      .value.toString();
    this.extendedWorkOrder.priority = this.form.get('priority').value;
    this.extendedWorkOrder.dueDate = this.form.get('dueDate').value;
    this.extendedWorkOrder.reportedOn = this.form.get('reportedOn').value;
    const categoryId = this.form.get('category').value;
    this.extendedWorkOrder.category = this.workOrderCategories.filter(
      category => category.id === Number(categoryId)
    )[0];
    this.extendedWorkOrder.serviceDetails = this.form
      .get('serviceDetails')
      .value.toString();
    this.showSaveButton = true;
    this.form.markAsDirty();
  }

  public selectedUserId(userId: string) {
    this.form.get('assignedToUser').setValue(userId);
    this.showSaveButton = true;
  }

  public updateWorkOrder() {
    debugger
    this.submitAttempted = true;
    if (!this.isUpdatingWorkOrder) {
      this.isUpdatingWorkOrder = true;
      this.isAssetAvailable =
        this.extendedWorkOrder.workOrderAssets.filter(
          asset => asset.isDelete !== true
        ).length > 0;

      if (!this.form.valid || !this.isAssetAvailable) {
        this.isNewFilesAdded = false;
        this.showSaveButton = false;
        this.isUpdatingWorkOrder = false;
       //  this.submitAttempted = false;
        this.form.markAsPristine();
        return;
      }
      this.showSaveButton = false;
      this.form.get('serviceDetails').setValue('');
      if (this.form.valid && this.form.dirty) {
        let paramWorkOrderAssets: IAssets[] = [];
        this.extendedWorkOrder.workOrderAssets.forEach(workOrderAssets => {
          if (workOrderAssets.isDelete === true || workOrderAssets.isNew) {
            workOrderAssets.referenceId = this.extendedWorkOrder.id;
            paramWorkOrderAssets.push(workOrderAssets);
          }
        });
        this.extendedWorkOrder.workOrderAssets = [];
        this.extendedWorkOrder.workOrderAssets = paramWorkOrderAssets;
        this.form.markAsPristine();
        this.workOrdersService
          .updateWorkOrderForId(
            this.extendedWorkOrder.id,
            this.extendedWorkOrder
          )
          .subscribe(
            data => {
              this.toastService.showToast(
                new Toast(
                  'Work Order (' + data.workOrderNumber + ') saved successfully'
                )
              );

              if (this.fileUploadForm.valid && this.isNewFilesAdded) {
                this.fileUploadService
                  .uploadFiles(
                    Number(this.extendedWorkOrder.number),
                    Modules.WorkOrder,
                    this.fileSrcCollection
                  )
                  .subscribe(
                    data => {
                      this.savedFileSrcCollection = data;
                      this.filePathCollection = data.attachments;
                      this.viewWorkOrderForId(false);
                      this.viewWorkOrderServiceDetailForId();
                    },
                    (errorResponse: HttpErrorResponse) => {
                      this.isUpdatingWorkOrder = false;
                      this.toastService.showErrorToast(
                        'Unable to save workorder files.'
                      );
                      return;
                    }
                  );
              } else {
                this.viewWorkOrderForId(false);
                this.viewWorkOrderServiceDetailForId();
                this.isUpdatingWorkOrder = false;
              }
              this.isNewFilesAdded = false;
              this.showSaveButton = false;
              this.extendedWorkOrder = data;
            },
            (errorResponse: HttpErrorResponse) => {
              this.isNewFilesAdded = false;
              this.showSaveButton = false;
              this.isUpdatingWorkOrder = false;
              const errorMessage = errorResponse[0];
              this.toastService.showErrorToast(errorMessage);
              return;
            },
            () => {
              this.errorMessage =
                'Sorry something went wrong... please try again later';
              this.isUpdatingWorkOrder = false;
              this.isNewFilesAdded = false;
              this.showSaveButton = false;
            }
          );
        return;
      }

      if (
        this.fileUploadForm.valid &&
        this.isNewFilesAdded &&
        !this.form.dirty
      ) {
        this.fileUploadService
          .uploadFiles(
            Number(this.extendedWorkOrder.number),
            Modules.WorkOrder,
            this.fileSrcCollection
          )
          .subscribe(data => {
            this.savedFileSrcCollection = data;
            if (this.savedFileSrcCollection.attachments) {
              this.filePathCollection = data.attachments;
              this.viewWorkOrderForId(false);
              this.viewWorkOrderServiceDetailForId();
              this.toastService.showToast(
                new Toast('Workorder files saved successfully')
              );
            } else {
              this.toastService.showErrorToast(`Files not saved`);
            }
            this.isUpdatingWorkOrder = false;
            this.isNewFilesAdded = false;
            this.showSaveButton = false;
          });
      }
      if (
        !this.fileUploadForm.valid &&
        this.isNewFilesAdded &&
        !this.form.dirty
      ) {
        this.toastService.showErrorToast(`Files not saved`);
      }

      this.isUpdatingWorkOrder = false;
      this.showSaveButton = false;
    }
  }

  public handleDeleteItem(assetIndex: number) {
    if (this.extendedWorkOrder && this.extendedWorkOrder.workOrderAssets) {
      this.extendedWorkOrder.workOrderAssets[assetIndex].isDelete = true;
      this.isAssetAvailable =
        this.extendedWorkOrder.workOrderAssets.filter(
          asset => asset.isDelete !== true
        ).length > 0;
      this.form.markAsDirty();
      this.showSaveButton = true;
    }
  }

  public handleFileInputChange(): void {
    this.showSaveButton = true;
    this.isNewFilesAdded = true;

    let noExistingFile = this.fileSrcCollection.length === 0;

    const inputFiles: any[] = this.fileUploadInput.nativeElement.files;
    for (let i = 0; i < inputFiles.length; i++) {
      if (
        this.fileSrcCollection.filter(file => file.name === inputFiles[i].name)
          .length === 0
      ) {
        this.fileSrcCollection.push(inputFiles[i]);

        this.filePathCollection.push('NewFile');
      } else {
        this.toastService.showErrorToast('Cannot upload duplicate file.');
      }
    }

    this.clearFileFormErrors();
    if (noExistingFile) {
      this.fileUploadForm.setValue({
        files: this.fileSrcCollection
      });
    } else {
      this.fileUploadForm.patchValue({
        files: this.fileSrcCollection
      });
    }

    if (
      this.fileUploadForm.controls['files'].hasError('acceptableFileType') ||
      this.fileUploadForm.controls['files'].hasError(
        'acceptableNumberOfFiles'
      ) ||
      this.fileUploadForm.controls['files'].hasError('acceptableFileSize')
    ) {
      this.showSaveButton = false;
    }
    this.fileCount = this.fileSrcCollection.length;
    this.attachedFileCollection.next(this.fileSrcCollection);
  }

  public handleDeleteFileAttached(fileIndex: number) {
    const filePath = this.filePathCollection[fileIndex];
    this.isUpdatingWorkOrder = true;
    if (filePath === 'NewFile') {
      this.fileSrcCollection.splice(fileIndex, 1);
      this.filePathCollection.splice(fileIndex, 1);
      this.fileCount = this.fileSrcCollection.length;
      this.attachedFileCollection.next([]);
      this.attachedFileCollection.next(this.fileSrcCollection);
      this.fileUploadForm.patchValue({
        files: this.fileSrcCollection
      });
      this.toastService.showToast(new Toast('File has been deleted.'));
      this.isUpdatingWorkOrder = false;
      this.resetSaveButtonDisplay();
    } else {
      this.fileUploadService.deleteFile(filePath, false).subscribe(
        () => {
          this.fileSrcCollection.splice(fileIndex, 1);
          this.filePathCollection.splice(fileIndex, 1);
          this.fileCount = this.fileSrcCollection.length;
          this.attachedFileCollection.next([]);
          this.attachedFileCollection.next(this.fileSrcCollection);
          this.toastService.showToast(new Toast('File has been deleted.'));
          this.isUpdatingWorkOrder = false;
          this.resetSaveButtonDisplay();
        },
        (err: HttpErrorResponse) => {
          this.toastService.showErrorToast('Unable to delete the file.');
          return;
        }
      );
      this.fileUploadForm.patchValue({
        files: this.fileSrcCollection
      });
    }

    this.fileUploadForm.markAsPristine();
    this.fileUploadForm.markAsUntouched();
    this.fileUploadForm.updateValueAndValidity();
  }

  public resetSaveButtonDisplay() {
    if (this.fileSrcCollection.length === 0) {
      this.showSaveButton = false;
    }
    if (this.fileSrcCollection.length === 3) {
      this.showSaveButton = true;
    }
  }

  public priorityString(priority: WorkOrderPriority) {
    return enumString(WorkOrderPriority, priority);
  }

  public handlePickAssets() {
    this.addAssetsModalVisible = true;
  }

  public handleAddedWorkOrderAssets(workOrderAsset: IAssets[]) {
    this.addAssetsModalVisible = false;
    this.extendedWorkOrder.workOrderAssets = workOrderAsset;
    this.isAssetAvailable =
      this.extendedWorkOrder.workOrderAssets.filter(
        asset => asset.isDelete !== true
      ).length > 0;
    this.form.markAsDirty();
    this.showSaveButton = true;
  }

  public handleClipboard(event: ClipboardEvent) {
    event.preventDefault();
    return;
  }

  public clearFileFormErrors() {
    this.fileUploadForm.controls['files'].setErrors({
      acceptableFileType: false,
      acceptableFileSize: false
    });
  }

  public getUserName(userDetails: string) {
    if (userDetails && userDetails.includes('-')) {
      return userDetails.split('-')[1];
    } else {
      return userDetails;
    }
  }
  public handleTextAreaInput() {
    this.isSubmitDisabled =
      this.selectedWorkOrderServiceDetails.serviceDetail.trim() === '';
  }
  public handleServiceSaveButton() {
    this.enableServiceTextarea = false;
    this.isLoading = true;
    this.workOrdersService
      .updateWorkOrderService(this.selectedWorkOrderServiceDetails)
      .subscribe(
        () => {
          this.viewWorkOrderServiceDetailForId();
          this.toastService.showToast(
            new Toast('Workorder service history saved successfully')
          );
          this.isLoading = false;
          this.detailsModalVisible = false;
        },
        (errorResponse: HttpErrorResponse) => {
          this.viewWorkOrderServiceDetailForId();

          this.toastService.showErrorToast(
            'Unable to save workorder service history'
          );
          this.isLoading = false;
          return;
        }
      );
  }

  public handleShowCheckListContent(checkList) {
    if (this.expanded === checkList) {
      this.expanded = null;
    } else {
      this.expanded = checkList;
    }
  }

  public handleSelectedChecklistTask() {
    this.showSaveButton = true;
    this.form.markAsDirty();
  }
}
