import { HttpErrorResponse } from '@angular/common/http';
import {
  Component,
  ElementRef,
  OnInit,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';
import { debounceTime, filter, map } from 'rxjs/operators';
import {
  AuthenticationService,
  IExtendedWorkOrder,
  ISkuStock,
  ISpares,
  IWorkOrderSpares,
  PermissionGroup,
  Role,
  SkuService,
  SparesGroup,
  Toast,
  ToastService,
  UserProfile,
  WorkOrdersPermissions,
  WorkordersService,
  WorkorderStatus
} from 'src/app/services';
import { stringCamelize } from 'src/utils/stringUtils';
import { WorkOrderWizard } from '../work-order.wizard';

@Component({
  selector: 'app-pick-workorder-spares',
  templateUrl: './pick-workorder-spares.component.html',
  styleUrls: ['./pick-workorder-spares.component.scss']
})
export class PickWorkOrderSparesComponent implements OnInit {
  public wordOrderNumber: string;
  public searchForm: FormGroup;
  public searchSparesSubscription: Subscription;
  public allSpareGroupOption = '';
  public sparesGroupOptions: Array<any> = [];
  public sparesGroups: Array<SparesGroup> = [];
  public WorkOrderStatus = WorkorderStatus;
  public isLoading: Boolean = true;
  public workOrderSpares: IWorkOrderSpares;
  public showSaveButton = false;
  public isUpdatingSpares = false;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public isSparesSelected = false;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  public searchResultsVisible = false;
  public searchResults: ISkuStock[] = [];
  public isSearching = false;
  public isEditable: boolean;
  public extendedWorkOrder: IExtendedWorkOrder;
  public isGettingUserRights = true;
  public isQtyFormInvalid = false;
  public workOrderStatus: string;

  constructor(
    private formBuilder: FormBuilder,
    private skuService: SkuService,
    private workOrderService: WorkordersService,
    private toastService: ToastService,
    private authenticationService: AuthenticationService,
    private workOrderWizard: WorkOrderWizard
  ) {
    this.workOrderStatus = this.workOrderWizard.workOrder.status.toString();
    this.wordOrderNumber = this.workOrderWizard.workOrder.number.toString();
    this.viewWorkOrderForId();
    this.getSpares(this.wordOrderNumber);
  }

  public ngOnInit(): void {
    this.getSearchGroupOptions();
  }

  public viewWorkOrderForId() {
    const workOrderNumber = this.workOrderWizard.workOrder.number.toString();
    this.workOrderService
      .viewWorkOrderForId(workOrderNumber)
      .subscribe(extendedWorkOrder => {
        this.extendedWorkOrder = extendedWorkOrder;
        this.checkUserEditRights();
      });
  }
  public searchSpares() {
    this.searchSparesSubscription &&
      !this.searchSparesSubscription.closed &&
      this.searchSparesSubscription.unsubscribe();
    const textValue = this.searchForm.get('text').value;
    if (this.isSparesSelected === false) {
      this.isSearching = true;
      this.searchResultsVisible = true;
      this.searchSparesSubscription = this.skuService
        .getSkuStockForSpares(
          this.searchForm.get('sparesGroup').value,
          textValue
        )
        .subscribe((skus: ISkuStock[]) => {
          this.searchResults = skus;
          this.isSearching = false;
        });
    }
  }

  public getSearchGroupOptions() {
    this.skuService.getSparesGroups().subscribe((data: Array<SparesGroup>) => {
      this.sparesGroups = data;
      this.sparesGroups.map((sparesGroup: SparesGroup) => {
        this.sparesGroupOptions.push({
          label: stringCamelize(sparesGroup.name),
          value: sparesGroup.groupId + '~' + sparesGroup.subGroupId
        });
      });
      this.isLoading = false;
    });
  }

  public handleDeleteSparesSku(isSkuDeleted: boolean, skuIndex: number) {
    this.showSaveButton = true;
    if (isSkuDeleted) {
      this.workOrderSpares.spareSkus.splice(skuIndex, 1);
    }
  }

  public getSpares(wordOrderId: string) {
    this.workOrderService
      .getWorkOrderSpares(wordOrderId)
      .subscribe(workOrderSpares => {
        const spareSkus: ISpares[] = [];
        workOrderSpares.spareSkus.forEach(sparesSku => {
          if (sparesSku) {
            const inStock = sparesSku.sku.quantities.inStock;
            const qty = sparesSku.quantity;

            sparesSku.sku.quantities.inStock = inStock + qty;
            spareSkus.push(sparesSku);
          }
        });

        this.workOrderSpares = {
          siteId: workOrderSpares.siteId,
          workOrderNumber: workOrderSpares.workOrderNumber,
          spareSkus: spareSkus
        };
      });
  }

  public handleSkuQtyChange(skuQty: number, skuIndex: number) {
    this.workOrderSpares.spareSkus[skuIndex].quantity = skuQty;
    this.workOrderSpares.spareSkus.forEach(spareSku => {
      if (spareSku.quantity > spareSku.sku.quantities.inStock) {
        this.showSaveButton = false;
        return;
      } else {
        this.showSaveButton = true;
      }
    });
  }

  public handleUpdateSpares() {
    this.isUpdatingSpares = true;
    this.workOrderService.updateWorkOrderSpares(this.workOrderSpares).subscribe(
      workOrderSpares => {
        this.workOrderSpares = workOrderSpares;
        this.isUpdatingSpares = false;
        this.showSaveButton = false;
        this.toastService.showToast(
          new Toast('Updated spares for this work order.')
        );
      },
      (err: HttpErrorResponse) => {
        this.toastService.showErrorToast('Unable to update spares.');
        this.isUpdatingSpares = false;
        this.showSaveButton = false;
      }
    );
  }

  public clearSearchForm() {
    this.searchForm.get('text').setValue('');
    this.searchForm.get('spares').setValue(null);
    this.searchResultsVisible = false;
  }

  public handleSearchKeyUp(event) {
    this.isSparesSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearSearchForm();
        break;
    }
  }

  public sparesNameIsInSearchResults(searchText: string) {
    let result = false;
    for (const spares of this.searchResults) {
      if (spares.id + ' - ' + spares.description === searchText) {
        result = true;
      }
    }
    return result;
  }

  public handleSearchBlur() {
    setTimeout(() => {
      const searchValue = this.searchForm.get('text').value;
      const activeElement: HTMLElement = document.activeElement as HTMLElement;
      if (
        this.sparesNameIsInSearchResults(searchValue) ||
        activeElement.dataset.sparesname
      ) {
        return;
      }
      this.clearSearchForm();
    }, 0);
  }

  public handleSearchResultKeyUp(event) {
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        event.target.nextSibling && event.target.nextSibling.focus();
        break;
      case 'Escape':
        this.clearSearchForm();
        break;
    }
  }

  public handleSearchResultClick(spares: ISkuStock) {
    if (
      this.workOrderSpares.spareSkus.filter(spare => spare.sku.id === spares.id)
        .length !== 0
    ) {
      this.toastService.showErrorToast(
        `Item ${spares.id} Spare already selected`
      );
      this.clearSearchForm();
      this.searchResultsVisible = false;
      this.isSparesSelected = true;
      return;
    }

    if (spares.quantities.inStock === 0) {
      this.toastService.showErrorToast(`Item ${spares.id} does not have stock`);
      this.clearSearchForm();
      this.searchResultsVisible = false;
      this.isSparesSelected = true;
      return;
    }

    this.workOrderSpares.spareSkus.push({
      sku: spares,
      quantity: spares.quantities.inStock > 0 ? 1 : 0
    });
    this.clearSearchForm();
    this.searchResultsVisible = false;
    this.isSparesSelected = true;
    this.showSaveButton = true;
  }

  private checkUserEditRights() {
    this.isGettingUserRights = true;
    const userRole$: Observable<UserProfile> = this.authenticationService.currentUserProfile$.pipe(
      map(userProfile => userProfile)
    );

    userRole$.subscribe(userProfile => {
      this.authenticationService
        .currentUserHasPermission({
          [PermissionGroup.WorkOrders]: [WorkOrdersPermissions.CanEditWorkOrder]
        })
        .subscribe(hasPermission => {
          if (userProfile.userRole.groupName !== Role.Technician) {
            this.isEditable =
              hasPermission &&
              Number(this.workOrderStatus) !== WorkorderStatus.Closed;
          } else {
            this.isEditable =
              hasPermission &&
              Number(this.workOrderStatus) !== WorkorderStatus.Closed &&
              Number(this.workOrderStatus) !== WorkorderStatus.Complete;
          }
          this.isGettingUserRights = false;
        });
    });

    this.searchForm = this.formBuilder.group({
      text: new FormControl({ value: '', disabled: !this.isEditable }),
      spares: null,
      sparesGroup: new FormControl({
        value: this.allSpareGroupOption,
        disabled: !this.isEditable
      })
    });
    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3),
        filter(textValue => !this.sparesNameIsInSearchResults(textValue))
      )
      .subscribe(() => this.searchSpares());
  }

  public handleInValidQty(isQtyFormInvalid: boolean) {
    this.isQtyFormInvalid = isQtyFormInvalid;
  }
}
