import { formatDate } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import {
  IWorkOrderCategory,
  Toast,
  ToastService,
  WorkOrderPriority,
  WorkordersService,
  IAssets,
  ICheckList
} from 'src/app/services';
import { enumString, enumValues } from 'src/utils/enumUtils';
import { WorkOrderWizard } from '../work-order.wizard';
import { MatChipInputEvent } from '@angular/material/chips';

@Component({
  selector: 'app-create-work-order',
  templateUrl: './create-work-order.component.html',
  styleUrls: ['./create-work-order.component.scss']
})
export class CreateWorkOrderComponent implements OnInit {
  public isLoading = false;
  public formCreateWorkOrder: FormGroup;
  public workOrderPriorities = enumValues(WorkOrderPriority);
  public showSaveButton = false;
  public workOrderCategories: IWorkOrderCategory[];
  public submitAttempted = false;
  public addAssetsModalVisible = false;
  public existingWorkOrderAssets: IAssets[] = [];
  public existingWorkOrderChecklists: ICheckList[] = [];
  public minDueDate = new Date();
  public todayDate = new Date();
  public pickCheckListsModalVisible = false;
  public checklists = [];

  constructor(
    private formBuilder: FormBuilder,
    private workOrderService: WorkordersService,
    private toastService: ToastService,
    public router: Router,
    private workOrderWizard: WorkOrderWizard
  ) {}

  public ngOnInit(): void {
    this.createForm();
    this.listCategory();
  }

  public createForm() {
    const bulidWorkOrderDetailForm = () => {
      this.formCreateWorkOrder = this.formBuilder.group({
        title: [
          '',
          Validators.compose([Validators.required, Validators.maxLength(50)])
        ],
        description: [
          '',
          Validators.compose([Validators.required, Validators.maxLength(4000)])
        ],
        category: ['', Validators.required],
        reportedOn: [this.todayDate, Validators.required],
        dueDate: [null, Validators.required],
        priority: ['', Validators.required],
        assignedToUser: ['', Validators.compose([Validators.maxLength(50)])]
      });
    };
    bulidWorkOrderDetailForm();
  }

  public createWorkOrder() {
    this.submitAttempted = true;
    this.showSaveButton = false;
    this.isLoading = true;
    if (
      formatDate(
        this.formCreateWorkOrder.value.dueDate,
        'yyyy-MM-dd',
        'en-US'
      ) <
      formatDate(
        this.formCreateWorkOrder.value.reportedOn,
        'yyyy-MM-dd',
        'en-US'
      )
    ) {
      this.formCreateWorkOrder.controls['dueDate'].setErrors({
        incorrect: true
      });
    }
    if (this.existingWorkOrderAssets.length === 0) {
      this.isLoading = false;
      return;
    }
    if (!this.formCreateWorkOrder.valid) {
      this.isLoading = false;
      return;
    }
    const workOrderAssets = this.existingWorkOrderAssets.map(x => x.id);
    const workOrderChecklists = this.existingWorkOrderChecklists.map(x => x.id);
    const assignedToUser = this.formCreateWorkOrder.value.assignedToUser;
    const title = this.formCreateWorkOrder.value.title;
    const description = this.formCreateWorkOrder.value.description;
    const priorityId = this.formCreateWorkOrder.value.priority;
    const categoryId = this.formCreateWorkOrder.value.category;
    const reportedOn = formatDate(
      this.formCreateWorkOrder.value.reportedOn,
      'yyyy-MM-ddThh:mm:ss',
      'en-US'
    );
    const dueDate = formatDate(
      this.formCreateWorkOrder.value.dueDate,
      'yyyy-MM-ddThh:mm:ss',
      'en-US'
    );
    this.workOrderService
      .createWorkOrder({
        assignedToUser,
        title,
        description,
        priorityId,
        categoryId,
        reportedOn,
        dueDate,
        workOrderAssets,
        workOrderChecklists
      })
      .subscribe(
        data => {
          this.toastService.showToast(
            new Toast(
              'Work order (' + data.workOrderNumber + ') created successfully'
            )
          );
          this.isLoading = false;
          this.workOrderWizard.workOrder = {
            id: data.id,
            number: data.number,
            status: data.status,
            assignedToUser: data.assignedToUser,
            dueDate: data.dueDate,
            createdTimestampUtc: data.createdTimestampUtc,
            lastUpdatedTimestampUtc: data.lastUpdatedTimestampUtc,
            title: data.title
          };
          this.router.navigate(['work-orders', data.number]);
        },
        err => {
          this.toastService.showErrorToast(`Work Order not saved`);
          this.isLoading = false;
        }
      );

    this.isLoading = false;
  }

  public selectedUserId(userId: string) {
    this.formCreateWorkOrder.get('assignedToUser').setValue(userId);
    this.showSaveButton = true;
  }

  public listCategory() {
    this.workOrderService
      .listWorkOrderCategory()
      .subscribe((data: IWorkOrderCategory[]) => {
        this.workOrderCategories = data;
      });
  }

  public onDateChange(inputDate) {
    this.minDueDate = new Date(inputDate.value);
    this.minDueDate.setDate(this.minDueDate.getDate());
    this.formCreateWorkOrder.get('dueDate').setValue(null);
  }

  public handlePickAssets() {
    this.addAssetsModalVisible = true;
  }

  public handleDetailsModalClose() {
    this.addAssetsModalVisible = false;
  }

  public disableRightClick(e) {
    e.preventDefault();
  }

  public handleAddedWorkOrderAssets(listAssets: IAssets[]) {
    this.addAssetsModalVisible = false;
    this.existingWorkOrderAssets = listAssets;
    this.showSaveButton = true;
  }

  public handleDeleteAsset(asset: IAssets) {
    this.existingWorkOrderAssets.splice(
      this.existingWorkOrderAssets.indexOf(asset),
      1
    );
    this.showSaveButton = true;
  }

  public priorityString(priority: WorkOrderPriority) {
    return enumString(WorkOrderPriority, priority);
  }

  public handlePickWorkOrderCheckLists(pickCheckLists: ICheckList[]) {
    this.pickCheckListsModalVisible = false;
    this.existingWorkOrderChecklists = pickCheckLists;
    this.checklists = pickCheckLists;
    this.showSaveButton = true;
  }

  public handlePickCheckLists() {
    this.pickCheckListsModalVisible = true;
  }

  public handlePickCheckListsModalClose() {
    this.pickCheckListsModalVisible = false;
  }

  public addCheckList(event: MatChipInputEvent) {
    const value = (event.value || '').trim();
    if (value) {
      this.checklists.push(value);
    }
    if (event.input) {
      event.input.value = '';
    }
  }
}
