import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { WorkOrdersComponent } from './work-orders.component';
import { ViewWorkorderComponent } from './view-workorder/view-workorder.component';
import { PickWorkOrderSparesComponent } from './pick-workorder-spares/pick-workorder-spares.component';
import { CreateWorkOrderComponent } from './create-work-order/create-work-order.component';

export const routes: Routes = [
  {
    path: '',
    component: WorkOrdersComponent
  },
  {
    path: 'create-work-order',
    component: CreateWorkOrderComponent
  },
  {
    path: ':id',
    component: ViewWorkorderComponent
  },
  {
    path: ':id/PickSpares',
    component: PickWorkOrderSparesComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkOrdersRoutingModule {}
