import { CommonModule, formatDate } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared-module/shared-module.module';
import { WorkOrdersRoutingModule } from './work-orders-routing.module';
import { WorkOrdersComponent } from './work-orders.component';
import { ViewWorkorderComponent } from './view-workorder/view-workorder.component';
import { MatDatepickerModule } from '@angular/material/datepicker';
import {
  MatNativeDateModule,
  NativeDateAdapter,
  DateAdapter,
  MAT_DATE_FORMATS
} from '@angular/material/core';
import { dateFormatWithMonthName } from 'src/utils/dateUtils';
import { PickWorkOrderSparesComponent } from './pick-workorder-spares/pick-workorder-spares.component';
import { SparesSkusComponent } from './pick-workorder-spares/spares-skus/spares-skus.component';
import { CreateWorkOrderComponent } from './create-work-order/create-work-order.component';
import { MatChipsModule } from '@angular/material/chips';
import { MatFormFieldModule } from '@angular/material/form-field';
import { ChecklistTaskItemComponent } from './view-workorder/checklist-task-item/checklist-task-item.component';

class PickDateAdapter extends NativeDateAdapter {
  format(date: Date, displayFormat: Object): string {
    if (displayFormat === 'input') {
      return formatDate(date, 'dd MMM yyyy', this.locale);
    } else {
      return formatDate(date, 'dd MMM yyyy', this.locale);
    }
  }
}
@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    SharedModule,
    WorkOrdersRoutingModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatChipsModule,
    MatFormFieldModule
  ],
  declarations: [
    WorkOrdersComponent,
    ViewWorkorderComponent,
    PickWorkOrderSparesComponent,
    SparesSkusComponent,
    CreateWorkOrderComponent,
    ChecklistTaskItemComponent
  ],
  providers: [
    { provide: DateAdapter, useClass: PickDateAdapter },
    { provide: MAT_DATE_FORMATS, useValue: dateFormatWithMonthName }
  ]
})
export class WorkOrdersModule {}
